import React from "react";
import "./Track.css";

class Track extends React.Component {
  constructor(props) {
    super(props);
    this.addCountry = this.addCountry.bind(this);
    this.removeCountry = this.removeCountry.bind(this);
  }
  renderAction() {
    if (this.props.isRemoval === true) {
      return <button className="Track-action" onClick={this.removeCountry}>-</button>;
    } else {
      return (
        <button className="Track-action" onClick={this.addCountry}>
          +
        </button>
      );
    }
  }
  addCountry() {
    this.props.onAdd(this.props.country);
  }
  removeCountry() {
    this.props.onRemove(this.props.country);
  }
  render() {
    return (
      <div className="Track">
        <div className="Track-information">
          <h3>Country</h3>
          <p>
            {this.props.country.country_id}|{this.props.country.probability}
          </p>
        </div>
        {this.renderAction()}
      </div>
    );
  }
}
export default Track;
