import React from "react";
import "./App.css";
import SearchBar from "../SearchBar/SearchBar";
import SearchResults from "../SearchResults/SearchResults";
import Playlist from "../Playlist/Playlist";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      searchResults: [{ country_id: "Ohio", probability: 0.1 }],
      playlistName: "My countrylist ",
      countryList: [{ country_id: "miamy", probability: 0.1 }],
    };
    this.addCountry = this.addCountry.bind(this);
    this.removeCountry = this.removeCountry.bind(this);
    this.updatePlaylistName = this.updatePlaylistName.bind(this);
    this.search = this.search.bind(this)
  }
  addCountry(country) {
    let countries = this.state.countryList;
    if (
      countries.find((savedCountry) => savedCountry.country_id === country.country_id)
    ) {
      return;
    }
    countries.push(country);
    this.setState({ countryList: countries });
  }
  removeCountry(country) {
    let countries = this.state.countryList;
    countries = countries.filter(
      (currentCountry) => currentCountry.country_id !== country.country_id
    );
    this.setState({ countryList: countries });
  }
  updatePlaylistName(name) {
    this.setState({ playlistName: name });
  }
  search(term) {
    fetch(`https://api.nationalize.io?name=${term}`)
      .then((res) => res.json())
      .then((js) => JSON.stringify(js))
      .then((st) => JSON.parse(st))
      .then((ob) => this.setState({ searchResults: ob.country }));
      
  }
  render() {
    return (
      <div>
        <h1>
          Ja<span className="highlight">mmm</span>ing
        </h1>
        <div className="App">
          <SearchBar onSearch={this.search} />
          <div className="App-playlist">
            <SearchResults
              searchResults={this.state.searchResults}
              onAdd={this.addCountry}
            />
            <Playlist
              playlistName={this.state.playlistName}
              countryList={this.state.countryList}
              onRemove={this.removeCountry}
              onNameChange={this.updatePlaylistName}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default App;
